<template>
<div class="movieCard">

<div class="app-tabs mb-5">
    <b-tabs content-class="mt-3">
        <b-tab title="Sinopsis" active >
            <b-card :title="movie.title" :img-src="url_poster+'/'+movie.poster_path" :img-alt="movie.title" img-left class="mb-3 app-card-movie">
                <b-card-text>
                    {{movie.overview}}
                </b-card-text>
                <WriteReview :movie_id="movie_id" class="mt-5" :token="token"/>
            </b-card>
        </b-tab>
        <b-tab title="Cast">
            <b-list-group>
                <b-list-group-item class="d-flex align-items-center" v-for="(cast,index) in credits" :key="index">
                <template v-if="cast.known_for_department === 'Acting'">
                    <b-avatar variant="info" :src="url_poster+'/'+cast.profile_path" class="mr-3"></b-avatar>
                    <span class="mr-auto">{{cast.name}} as <b> {{cast.character}} </b></span>
                </template>
                </b-list-group-item>
            </b-list-group>
        </b-tab>
  </b-tabs>
  
</div>

</div>
</template>

<script>

const api_url = process.env.VUE_APP_API_URL;
import WriteReview from '../shared/buttons/Write-review';

export default {
    name:'MovieCard',
    props:{
        movie_id: String | Number,
        token: String
    },
    components:{
        WriteReview
    },
    data(){
        return{
            movie: '',
            credits: '',
            url_poster : process.env.VUE_APP_API_POSTER,
        }
    },
    created(){
        this.init();
    },
  methods:{
      init(){
          this.getMovie();
      },

  
  async getMovie(){
    try{
      let response = await this.axios.get(`${api_url}/movie/${this.movie_id}`);
        this.movie = response.data.movie;
        this.credits = response.data.credits.cast;
        // console.log(response.data);
      }catch(error){
        console.log(error);
      }
    }
  }
}
</script>

<style scoped>

.app-card-movie {
    padding: 2em;
    border: 0px;
}

.app-card-movie img{
    border-radius: 0.3em;
    width: 15em;
    height: 20em;
}

.card-title{
    text-align:center;
}

.app-tabs {
    margin-top: 1em;
    margin-right: 2em;
    margin-left: 2em;
}

</style>