<template>
<div class="writeReview" v-if="!reviewPath()">
    <template v-if="reviewExist">
        <p>
        <small> You has already review this movie!</small>
        </p>
          <b-button @click="goUpdateReview()" variant="info"> 
            <b-icon icon="pencil-square"></b-icon> Update review 
        </b-button>
    </template>
    <template v-else>
        <b-button @click="goReviewMovie()" class="app-button"> 
            <b-icon icon="pencil-square"></b-icon> Write a review 
        </b-button>
    </template>
</div>
</template>

<script>
const api_url = process.env.VUE_APP_API_URL;

export default {
    name: 'WriteReview',
    props:{
        movie_id: String | Number,
        token: String
    },
    data(){
        return{
            reviewExist: ''
        }
    },

    methods:{
        goReviewMovie(){
            this.$router.push({path: `/review/movie/${this.movie_id}`});
        },
        reviewPath(){
            return (this.$route.path == `/review/movie/${this.movie_id}` ? true : false)
        },
        goUpdateReview(){
            this.$router.push({path: `/review/update/${this.reviewExist._id}`});
        }
    },
    created:function(){
        if(this.token){
        this.axios.get(`${api_url}/review/for/${this.movie_id}/user`,{headers:{
            authorization: this.token
        }}).then((response)=>{
            this.reviewExist = response.data.review[0];
        }).catch((error)=>{
            console.log(error);
        })
        }
    }
}
</script>

